import React, { useState, useEffect } from 'react';
import {
  useTheme,
  Container,
  Box,
  Typography,
  Stack,
  FormControlLabel,
  RadioGroup,
  Radio,
  TextField,
  InputAdornment,
  Button
} from '@mui/material';

import mainBackground from '../../assets/images/main-background.jpg';

const Hero = (): JSX.Element => {
  const theme = useTheme();

  const [betType, setBetType] = useState(0);
  const [betAmount, setBetAmount] = useState('100');

  useEffect(() => {
    const jarallaxInit = async () => {
      const jarallaxElems = document.querySelectorAll('.jarallax');
      if (!jarallaxElems || (jarallaxElems && jarallaxElems.length === 0)) {
        return;
      }

      const { jarallax } = await require('jarallax');
      jarallax(jarallaxElems, { speed: 0.2 });
    };

    jarallaxInit();
  });

  return (
    <Box
      className={'jarallax'}
      sx={{
        height: '105vh',
        marginTop: -10,
        paddingTop: { xs: 20, sm: 15, md: 10 },
        paddingBottom: 5,
        position: 'relative',
        display: 'flex',
        alignItems: 'center'
      }}
    >
      <Box
        className={'jarallax-img'}
        sx={{
          position: 'absolute',
          objectFit: 'cover',
          fontFamily: 'object-fit: cover;',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: -1,
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover',
          backgroundPosition: 'center center',
          backgroundImage: 'url(' + mainBackground + ')',
        }}
      />
      <Container>
        <Box
          sx={{
            padding: 5,
            borderRadius: 2.5,
            backgroundColor: theme.palette.background.paper,
            opacity: .9
          }}
        >
          <RadioGroup
            row
            aria-labelledby="demo-row-radio-buttons-group-label"
            name="row-radio-buttons-group"
            defaultValue="1"
            sx={{
              marginBottom: 2
            }}
          >
            <Stack spacing={1}>
              <Box>
                <Typography component={'h6'}>
                  {' 2 '}
                </Typography>
                <FormControlLabel value="1" control={<Radio />} label="" />
                <FormControlLabel value="2" control={<Radio />} label="" />
                <FormControlLabel value="3" control={<Radio />} label="" />
                <FormControlLabel value="4" control={<Radio />} label="" />
              </Box>
              <Box>
                <Typography component={'h6'}>
                  {' 4 '}
                </Typography>
                <FormControlLabel value="6" control={<Radio />} label=" + " />
                <FormControlLabel value="7" control={<Radio />} label=" + " />
                <FormControlLabel value="8" control={<Radio />} label=" + " />
                <FormControlLabel value="9" control={<Radio />} label=" + " />
              </Box>
              <Box>
                <Typography component={'h6'}>
                  {' 8 '}
                </Typography>
                <FormControlLabel value="0" control={<Radio />} label="" />
                <FormControlLabel value="5" control={<Radio />} label="" />
              </Box>
            </Stack>
          </RadioGroup>
          <TextField
            type="number"
            value={ betAmount }
            onChange={ (event: React.ChangeEvent<HTMLInputElement>) => { setBetAmount(event.target.value); } }
            InputProps={{
              endAdornment: <InputAdornment position="end">USDT</InputAdornment>,
            }}
            label=""
            helperText=" 100-10000 USDT"
            size="small"
            fullWidth
            sx={{
              marginBottom: 2
            }}
          />
          <Button
            variant="contained"
            size="large"
            // onClick={ donateAction }
          >
            {''}
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default Hero;
